<template>
    <div id="app">
        <b-form-input v-model="schoolname" @keyup.enter ="submit" placeholder="학교이름"></b-form-input>
        <School v-for="school in schools" v-bind:key="school.school_name" v-bind:school="school"></School>
    </div>
</template>
<script>
import School from './School.vue';
export default {
  components: { 
      School 
      },
    name: "Search",
    data() {
        return {
            schoolname: '',
            schools: [
            ]
        }
    },
    methods : {
        submit:function(){
            let url = "/" + this.schoolname
            this.$axios.get(url).then((res) => {
                if(res.data.length >= 20){
                    alert("학교 이름을 좀 더 상세하게 입력해주세요.");
                    return;
                }
                let schoolObject = new Object;
                let schoolArray = [];
                for(let i = 0; res.data[i] != undefined; i++){
                   schoolObject.school_name = res.data[i].school_name;
                   schoolObject.school_code = res.data[i].school_code;
                   schoolArray.push(schoolObject);
                }
                console.log(schoolArray);
                this.schools = schoolArray;
            });
        }
    }
}
</script>

<style lang="scss">
#app {
  margin-top: 60px;
  margin-bottom: 60px;
}
</style>